import React from 'react'
import { Table, Tag } from 'antd';
import "./index.scss"
export default function ProgramContent() {

    const columns = [
        {
          title: '',
          dataIndex: 'name',
          key: 'name',
          render: (_,record) => <a target="_blank" href={record.url}>{record.name}</a>,
        },
        {
          title: 'Stars',
          dataIndex: 'stars',
          key: 'stars',
        },
        {
          title: 'Shares',
          dataIndex: 'shares',
          key: 'shares',
        },
        {
          title: '',
          key: 'tags',
          dataIndex: 'tags',
          render: (_, { tags }) => (
            <>
              {tags.map((tag) => {
                return (
                  <Tag color='geekblue' key={tag}>
                    {tag}
                  </Tag>
                );
              })}
            </>
          ),
        },
      ];
      const data = [
        {
          key: '1',
          name: '',
          url:"https://github.com/Ronald-Kong/Project_CMS",
          stars: 1,
          shares: 0,
          tags: ['javascript', 'koa2',],
        },
        {
          key: '2',
          name: '',
          url:"https://github.com/Ronald-Kong/myblog",
          stars: 0,
          shares: 0,
          tags: ['javascript', 'html'],
        }
      ];

  return (
        <div id='content_container'>
            <div id="project_outer_container">
                <span></span>
                <div id="project_inner_container">
                <Table columns={columns} dataSource={data} pagination={{position:["bottomCenter"]}}/>
                </div>
            </div>
        </div>
  )
}
